.hero {
  position: relative;
  height: 100vh;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100vh;
    background:
      linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.5)),
      radial-gradient(66.48% 66.48% at 50% 50%, rgba(24, 33, 31, 0) 0%, rgba(24, 33, 31, 0.8) 100%);
  }

  &__img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    object-fit: cover;
  }

  .container {
    position: absolute;
    left: 50%;
    bottom: 20%;
    z-index: 10;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    transform: translateX(-50%);

    @include m(1290) {
      flex-direction: column;
      align-items: center;
    }

    @include m($md) {
      bottom: 48px;
    }
  }

  h1 {
    max-width: 619px;
    color: #fff;

    @include m(1600) {
      max-width: 550px;
      font-size: 66px;
    }

    @include m(1385) {
      font-size: 62px;
    }

    @include m(1290) {
      margin-bottom: 24px;
      max-width: 100%;
      text-align: center;
    }

    @include m($xl) {
      font-size: 54px;
    }

    @include m($lg) {
      font-size: 45px;
    }
  }

  &__present {
    margin-left: 48px;
    padding: 48px;
    width: 100%;
    height: fit-content;
    max-width: 885px;
    background-color: rgba(24, 33, 31, 0.9);

    ul {
      display: grid;
      grid-gap: 0 48px;
      grid-template-columns: 1fr 1fr 1fr;

      li {
        position: relative;

        img {
          margin-bottom: 24px;

          @include m($md) {
            position: absolute;
            left: 0;
            top: 0;
            margin: 0;
          }
        }

        h5 {
          margin-bottom: 8px;
          color: #fff;
        }

        p {
          font-size: 14px;
        }

        @include m($md) {
          padding-left: 56px;
        }
      }

      @include m(1600) {
        grid-gap: 0 32px;
      }

      @include m($lg) {
        grid-gap: 0 24px;
      }

      @include m($md) {
        grid-gap: 48px 0;
        grid-template-columns: 1fr;
      }
    }

    @include m(1600) {
      padding: 32px;
    }

    @include m(1385) {
      margin-left: 32px;
    }

    @include m(1290) {
      margin: 0 auto;
    }

    @include m($lg) {
      padding: 24px;
    }
  }
}

.we {
  padding: 240px 0 120px;

  img {
    width: 100%;
    min-height: 138px;
    object-fit: cover;
  }

  h2 {
    margin-bottom: 48px;
    text-align: center;
    color: #fff;

    @include m($lg) {
      margin-bottom: 32px;
    }
  }

  p {
    margin: 0 auto 120px;
    max-width: 1094px;
    text-align: center;

    &:not(:last-of-type) {
      margin-bottom: 24px;
    }

    @include m($xl) {
      margin: 0 auto 80px;
    }

    @include m($lg) {
      margin: 0 auto 40px;
    }
  }

  @include m($xl) {
    padding: 200px 0 90px;
  }

  @include m($lg) {
    padding: 180px 0 60px;
  }
}

.gallery {
  padding: 120px 0;

  h2 {
    margin-bottom: 80px;
    text-align: center;

    @include m($xl) {
      margin-bottom: 55px;
    }

    @include m($lg) {
      margin-bottom: 32px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    img {
      width: 100%;
      height: 641px;
      max-height: 641px;
      object-fit: cover;

      @include m(1500) {
        height: 550px;
      }

      @include m(1400) {
        height: 480px;
      }

      @include m($xl) {
        height: 400px;
      }

      @include m($lg) {
        height: 300px;
      }

      @include m($md) {
        height: 200px;
      }
    }

    a {
      margin-bottom: 40px;
      width: 100%;

      &:first-child {
        img {
          @include m(627) {
            height: 454px;
          }
        }
      }

      &:last-child {
        img {
          @include m(627) {
            height: 454px;
          }
        }
      }

      @include m(1500) {
        margin-bottom: 30px;
      }
    }
  }

  &__small {
    max-width: 32%;

    img {
      padding-right: 20px;

      @include m(1500) {
        padding-right: 15px;
      }

      @include m(627) {
        padding-right: 0;
      }
    }

    @include m(1500) {
      max-width: 40%;
    }

    @include m($lg) {
      max-width: 50%;
    }

    @include m(627) {
      max-width: 100%;
    }
  }

  &__big {
    max-width: 68%;

    img {
      padding-left: 20px;

      @include m(1500) {
        padding-left: 15px;
      }

      @include m(627) {
        padding-left: 0;
      }
    }

    @include m(1500) {
      max-width: 60%;
    }

    @include m($lg) {
      max-width: 50%;
    }

    @include m(627) {
      max-width: 100%;
    }
  }

  &__medium {
    max-width: 50%;

    @include m(627) {
      max-width: 100%;
    }
  }

  &__medium--first {
    img {
      padding-right: 20px;

      @include m(1500) {
        padding-right: 15px;
      }

      @include m(627) {
        padding-right: 0;
      }
    }
  }

  &__medium--second {
    img {
      padding-left: 20px;

      @include m(1500) {
        padding-left: 15px;
      }

      @include m(627) {
        padding-left: 0;
      }
    }
  }

  &__big--revert {
    img {
      padding-right: 20px;
      padding-left: 0;

      @include m(1500) {
        padding-right: 15px;
      }

      @include m(627) {
        padding-right: 0;
      }
    }
  }

  &__small--revert {
    img {
      padding-right: 0;
      padding-left: 20px;

      @include m(1500) {
        padding-left: 15px;
      }

      @include m(627) {
        padding-left: 0;
      }
    }
  }

  .rv-explore {
    margin: 8px auto 0;

    @include m(1500) {
      margin: 18px auto 0;
    }
  }

  @include m($xl) {
    padding: 90px 0;
  }

  @include m($lg) {
    padding: 60px 0;
  }
}

.sale {
  position: relative;
  z-index: 10;
  padding: 120px 0;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-color: rgb(24, 33, 31);
    filter: blur(80px);

    @include m($lg) {
      filter: blur(40px);
    }

    @include m($md) {
      display: none;
    }
  }

  .container {
    z-index: 5;
  }

  h2 {
    z-index: 10;
    margin-bottom: 40px;
    text-align: center;
    color: #fff;
  }

  &__descr {
    margin: 0 auto 80px;
    max-width: 630px;
    text-align: center;

    @include m(1500) {
      margin: 0 auto 60px;
    }

    @include m($xl) {
      margin: 0 auto 55px;
    }

    @include m($lg) {
      margin: 0 auto 48px;
      font-size: 18px;
    }
  }

  .sale__tags {
    flex-wrap: wrap;
    margin: 0;

    div {
      margin-right: 16px;
      margin-bottom: 16px;

      + div {
        margin-left: 0;
      }
    }
  }

  ul {
    display: grid;
    grid-gap: 0 48px;
    grid-template-columns: 1fr 1fr 1fr;
    margin-bottom: 80px;

    a {
      display: flex;
      flex-direction: column;
      min-height: 548px;

      &:hover {
        .sale__button {
          background-position: bottom -1px center;
        }

        .sale__link {
          opacity: 1;
        }

        .sale__img {
          img:first-child {
            transform: scale(1.1);
          }
        }
      }

      @include m(850) {
        min-height: 507px;
      }
    }

    p {
      margin-bottom: 18px;
      font-size: 14px;
    }

    .sale__img {
      position: relative;
      margin-bottom: 16px;
      overflow: hidden;

      img:first-child {
        width: 100%;
        height: 100%;
        min-height: 216px;
        max-height: 304px;
        object-fit: cover;
        transition: all 0.3s;
      }
    }

    .sale__link {
      position: absolute;
      right: 8px;
      bottom: 8px;
      opacity: 0;
      transition: all 0.3s;

      @include m($xl) {
        right: 4px;
        bottom: 4px;
        transform: scale(0.8);
      }
    }

    .sale__title {
      display: flex;
      justify-content: space-between;
      margin-bottom: 16px;

      h3 {
        margin-right: 15px;
        color: #fff;

        @include m($xl) {
          font-size: 20px;
        }
      }

      h5 {
        min-width: fit-content;
        color: #fbd394;
      }
    }

    @include m(1400) {
      grid-gap: 0 24px;
    }

    @include m($xl) {
      grid-gap: 0 12px;
      margin-bottom: 60px;
    }

    @include m(850) {
      grid-gap: 48px 0;
      grid-template-columns: 1fr;
      margin-bottom: 48px;
    }
  }

  @include m($xl) {
    padding: 90px 0;
  }

  @include m($lg) {
    padding: 60px 0;
  }
}

.custom {
  padding: 120px 0 240px;

  h2 {
    margin: 0 auto 48px;
    max-width: 751px;
    text-align: center;
    color: #fff;

    span {
      color: #f3d697;
    }
  }

  p {
    margin: 0 auto 48px;
    max-width: 751px;
    text-align: center;

    @include m($lg) {
      font-size: 18px;
    }
  }

  ul {
    display: grid;
    grid-gap: 0 48px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 120px;

    li {
      min-height: 103px;
      overflow: hidden;
      transition: all 0.3s;

      img {
        width: 100%;
        height: 100%;
        min-height: 103px;
        object-fit: cover;
      }

      &:hover {
        transform: scale(1.071);
      }

      &:first-child {
        border-radius: 175px 0 0 175px;
      }

      &:nth-child(2) {
        border-radius: 175px;
      }

      &:nth-child(3) {
        border-radius: 0 175px 0 0;
      }

      &:nth-child(4) {
        border-radius: 175px 175px 0 0;
      }
    }

    @include m($xl) {
      grid-gap: 0 28px;
    }

    @include m($lg) {
      grid-gap: 0 20px;
    }

    @include m($md) {
      grid-gap: 0 16px;
    }
  }

  .rv-button {
    p {
      margin: 0 !important;
    }
  }

  @include m($xl) {
    padding: 90px 0 180px;
  }

  @include m($lg) {
    padding: 60px 0 120px;
  }
}

.sale__tags {
  display: flex;
  margin-bottom: 16px;

  div {
    display: flex;
    align-items: center;
    height: 20px;

    p {
      margin-bottom: 0;
      font-family: "Causten-500", sans-serif;
      font-weight: 500;
      font-size: 12px;
      line-height: 14px;
      letter-spacing: 0.04em;
      color: #b5b1ad;
    }

    img {
      margin-right: 4px;
      width: 20px;
      height: 20px;
    }

    + div {
      margin-left: 16px;
    }
  }
}

.sale__button {
  position: relative;
  margin: auto 0 0;
  padding: 16px;
  border-bottom: 1px solid #324541;
  font-family: "Causten-600", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.1;
  letter-spacing: 0.04em;
  text-align: left;
  text-transform: uppercase;
  color: #fff;
  background-image: linear-gradient(to top, #324541 50%, transparent 50%);
  background-position: top;
  background-size: 100% 200%;
  transition: all 0.3s;

  &::after {
    content: url("../images/sale-arrow.svg");
    position: absolute;
    right: 8px;
    top: 50%;
    width: 48px;
    height: 48px;
    transform: translateY(-50%);
  }
}
